body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.customSpin {
  position: absolute;
  left: 50%;
  top: 50%;
}
@media only screen and (max-width: 600px) {
  .customSpin {
    left: 45%;
  }
}
ul,li {
  margin: 0px;
  padding: 0px;
}

footer{
  position: absolute;
  bottom:0px;
  background-color: var(--bs-dark);
  color: white;
  padding: 1rem;
  text-align: center;
  width: 100%;
}
footer>a{
 
list-style: none;
font-style: unset;
text-decoration: unset;
color: red;
font-weight: 500;
font-size: medium;
}
footer>a:hover{
  list-style: none;
  text-decoration: none;
  color: red;
  font-weight: 500;
  font-size: medium;
  }

  .card-header{
    background-color: var(--bs-dark);
    color: white;
  }

  .form-control:focus {
    color: #212529;
    background-color: #fff;
    border-color: #212529;
    outline: 0;
    box-shadow: 0 0 0 0.05rem #212529;
}